

.banner{
    background-color: black;
    height: 600px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        background-color: black;
        opacity: 0.4;
    }

    > p,h2{
        color: white;
        font-size: 3rem;
        width: 50%;
        z-index: 10;
        font-weight: 400;
        margin: 1rem;
    }

    > p{
        font-size: 1rem;
        font-weight: 200;
        letter-spacing: 2px;
        word-spacing: 2px;
    }

    >div{
        display: flex;
    }

    button{
        border: none;
        margin: none;
        padding: 1rem;
        width: 150px;
        z-index: 10;
        border-radius: 5px;
        margin: 1rem;
        background-color: white;
        font-size: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all 0.1s ease;

        &:hover{
            background-color: rgb(185, 178, 178);
        }

        svg{
            font-size: 1.5rem;
            margin: 0 10px;
        }
    }
}

.row{
    padding: 1rem;
    background-color: black;
    >h2{
        margin: 0.3rem;
        color: white;
        font-weight: 400;
    }
    > div{
        display: flex;
        overflow-x: auto;
    }
}

.card{
    width: 200px;
    cursor: pointer;
    margin: 0.3rem;
    transition: transform 0.5s;
    &:hover{
        transform: translateY(-10px);
    }
}

