
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

*{
    margin: 0%;
    padding: 0%;
    font-family: 'Roboto',sans-serif;

    &::-webkit-scrollbar{
        display: none;
    }
}



.header{
    height: 60px;
    background-color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    padding: 1rem;
    box-sizing: border-box;

    > img{
        height: 100%;
    }

    > div{
        width: 100%;
        margin-left: 5vmax;
        >a{
            margin: 0% 3rem;
            font-size: 1.1rem;
            font-weight: 300;
            color: white;
            text-decoration: none;
        }

    }

    > svg{
        color: white;
        font-size: 2rem;
        margin-right: 1rem;
        cursor: pointer;

        &:hover{
            color: rgb(181, 175, 175);
        }
    }
}

.genrebox{
    height: 100px;
    background-color: black;
    display: flex;
    overflow-x: auto;
    > a{

        height: fit-content;
        margin: auto 1%;
        font-size: 1.3rem;
        text-decoration: none;
        color: rgb(3, 3, 3);
        background-color: white;
        padding: 10px;
        border-radius: 5px;
        text-align: center;

        &:hover{
            background-color: rgb(211, 210, 210);
        }
    }
}

